//! css framework
// @import "../node_modules/bulma/bulma";

//! utilities
@import "./styles/utils/mixins"; // global mixins
@import "./styles/utils/variables"; // global variables

//! setup
@import "./styles/setup/normalize"; // normalize browsers to make more consistent
@import "./styles/setup/clearfix"; // clearfix for floats
@import "./styles/setup/border-box"; // global border-box setting

//! global styles
@import "./styles/global/typography"; // global typography
@import "./styles/global/global"; // global styles

//! sections
@import "./styles/layout/header";

.hidden {
  display: none;
}


.main-view-wrapper {
    display: flex;
    max-width: 90%;
    margin: 0 auto;
}

.movies-list-wrapper {
    border: 1px solid black;
    min-width: 75%;
}

.movies-list-title {
    text-align: center;
    margin: 10px 0;
}

ul#movies-list {
    display: flex;
    flex-flow: row wrap;
    
    justify-content: center;
}

.movies-list-item {
    display: inline-block;
    float: left;   
    max-width: calc(100% * (1/4) - 40px - 1px);
    flex: 1 0 21%;
    text-align: center;
    margin: 20px 20px;
    border: 1px solid rgb(24, 19, 19);
    border-radius: 10px;
    background-color: rgb(145, 145, 145);
    padding: 20px;
    cursor: pointer;
}


 .movies-list-pic {
    display: block;
    height: 100px;
    width: 100%;
    object-fit: cover;
    object-position: center;
    display: inline-block;
    border: 1px solid rgb(24, 19, 19);
}

.movies-list-item > div {
    margin-top: 10px;
}



.modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.9);
}
.modal-content {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
}

/* Caption of Modal Image */
#modal-caption {
  margin: auto;
  display: flex;
  width: 80%;
  max-width: 700px;
  text-align: center;
  color: #181313;
  padding: 10px 10px;
  
}

/* Add Animation */
.modal-content, #modal-caption {  
  animation-name: zoom;
  animation-duration: 0.6s;
}

@keyframes zoom {
  from {transform: scale(0.1)} 
  to {transform: scale(1)}
}

/* The Close Button */
.close {
  color: #181313;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
  left: 95%;
  position: relative;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

.movie-modal-wrap {
  width: 60%;
  margin: 0 auto;
  background-color: #919191;
}

.movie-modal-inner.inner-right > div{
  margin: 25px 0px;
  text-align: left;
  margin-left: 40px;
}
.movie-modal-inner.inner-left > div {
  margin: 25px 0px;
}

.modal-movie-img {
  display: block;
  height: 60%;
  width: 90%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
  display: inline-block;
  border: 1px solid #181313;
}

.movie-modal-genres {
    display: flex;
    flex-wrap: wrap;

}

.movie-modal-genre-single {
    flex: 1 0 26%;
    flex: 1 0 32%;
    margin-top: 10px;
    background-color: #bbb;
    padding: 4px;
    margin-right: 10px;
}

.favourites-list-wrapper {
  min-width: 20%;
  margin-left: 20px;
  border: 1px solid black;
}
.favourites-list-title h1 {
  text-align: center;
  margin: 10px 0;
}


.favorite-star {
    width: 30px;
    height: 30px;
    font-size: 30px;
    float: right;      
    color: rgba(255, 217, 0, 0.856);
}




.favorite-star:hover {
  color: rgb(255, 217, 0);
}

.favorite-star.star-inactive{
  //content: "★";
  color: rgba(0, 0, 0, 0.856);

}



.favorite-star.star-active{
  //content: "★";
  color: rgba(255, 217, 0, 0.856);

}


.favorite-star:hover {
  color: rgba(231, 219, 148, 0.856);
}


.favourites-list-item{
  margin: 10px 15px;
}
.favourites-list-item-name {
  display: inline-block;
  max-width: 90%;
}

.favourites-list-item-name:before {
  content: '►';
  margin-right: 10px;
}

.favourites-list-item-remove {
  float: right;
  width: 10%;
}

.favourites-list-item-remove:after {
  content: '✕';
  cursor: pointer;
  text-shadow: rgb(24, 19, 19);
}

.favourites-list-item-remove:hover {
  color: rgb(156, 18, 18);
  transform: scale(1.1);

}